.home {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
}

.home__header {
  color: white;
  align-self: stretch;
}

.home__header h1 {
  font-style: normal;
  font-weight: normal;
  text-align: center;
  font-size: 24px;
  line-height: 28px;
}

.home__main {
  display: flex;
  flex-direction: column;
  align-content: center;

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  color: #adadad;
  text-align: center;
}

.home__header-message h1 {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.home__control {
  margin-bottom: 50px;
}

.home__control-button {
  display: flex;
  background: none;
  justify-content: center;
  cursor: pointer;
}

.questionnaire {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  box-sizing: border-box;
}

.questionnaire__diagram-wrapper {
  display: flex;
  justify-content: center;
}

.questionnaire__diagram {
  color: white;
  font-weight: 700;
  height: 120px;
  width: 120px;
  border-radius: 50%;
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  box-sizing: border-box;
  border: 10px solid;
}

.questionnaire__question {
  color: white;
  font-weight: normal;
  font-size: 24px;
  line-height: 23px;
  text-align: center;
}

.questionnaire__options {
  display: flex;
  flex-direction: column;
  margin: 0 30px;
}

.questionnaire__option {
  padding: 16px 20px;
  color: black;
  background-color: white;
  display: flex;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.0416667);
  border-radius: 3px;
  cursor: pointer;
  margin: 16px 0;
  font-size: 20px;
  font-weight: 500;
  transition: 0.1s linear;
  -webkit-transition: 0.1s linear;
  -moz-transition: 0.1s linear;
  -o-transition: 0.1s linear;
}

.questionnaire__option-active {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.questionnaire__option-active:before {
  content: "";
  margin-top: 5px;
  margin-right: 10px;
  width: 16px;
  height: 16px;
  background: url(/vk-mini-app__orbi/static/media/checkicon.710340ba.svg);
}

.questionnaire__control {
  margin-bottom: 50px;
}

.questionnaire__control-button {
  display: flex;
  background: none;
  justify-content: center;
  cursor: pointer;
}

.Panel__in {
  background: -webkit-gradient(linear, left top, left bottom, from(#2b00a5), to(#110041)), #ffffff;
  background: -webkit-linear-gradient(top, #2b00a5 0%, #110041 100%), #ffffff;
  background: linear-gradient(180deg, #2b00a5 0%, #110041 100%), #ffffff;
  height: 100%;
}

.PanelHeader__container,
.PanelHeader-right,
.PanelHeader-left-in,
.PanelHeader-content {
  background-color: #2b00a5;
}

.Group {
  margin: 10px 10px;
  border-radius: 5px;
}

.Group:before,
.Group:after {
  display: none;
}

